	module.exports = {"data":{"markdownRemark":{"html":"<h2>P Align Attribute</h2>\n<p>The <code>&#x3C;p></code> tag align attribute is a way to align text to a specific location on the page. The supported values are left, right, center, and justify. For example you can write <code>&#x3C;p align=\"right\">My Text Here&#x3C;/p></code> and the text will be aligned to the right side of the page. The align attribute is not supported in HTML 5 the latest version of HTML. It’s recommended to use CSS to align text in HTML 5.</p>\n<h4>More Information:</h4>\n<ul>\n<li><a href=\"https://www.w3.org/TR/html401/struct/text.html#h-9.3.1\">W3 - HTML 4.01 Specification</a></li>\n<li><a href=\"https://developer.mozilla.org/en-US/docs/Web/CSS/text-align\">MDN - CSS Text Align</a></li>\n</ul>","fields":{"slug":"/html/attributes/p-align-attribute/"},"frontmatter":{"title":"P Align Attribute"}}},"pathContext":{"slug":"/html/attributes/p-align-attribute/"}}

